// patterns.less
// -----------------------------------------------------------------

.hatch-logo {
  color: @brand-secondary;
  font-family: @font-family-klavika-bold;
  text-transform: uppercase;
}

.vertical-center {
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -moz-transform: translateY(-50%);
  transform: translateY(-50%);
}

.vertical-center-parent {
  -webkit-transform-style: preserve-3d;
  -moz-transform-style: preserve-3d;
  transform-style: preserve-3d;
}

.alert {
  ul {
    margin-top: 10px;
  }
  strong {
    font-weight: normal;
    
  }
}

form {
  input[type="text"],
  input[type="tel"],
  input[type="email"] {
    height: 40px;

  }
  .btn-primary {
    height: 50px;
    line-height: 50px;
    font-size: 18px;
    font-weight: 500;
    padding: 0 65px;
    margin-top: 30px;
  }
}

.ninja-forms-required-items {
  display: none;
}

.ninja-forms-success-msg {
  padding: 15px;
  margin-bottom: 20px;
  border: 1px solid transparent;
  border-radius: 4px;
  color: #3c763d;
  background-color: #dff0d8;
  border-color: #d6e9c6;
  p {
    margin: 0;
  }
  p:first-child {
    display: none;
  }
  p:last-child {
    display: none;
  }
}

.ninja-forms-field-error {
  margin-bottom: 20px;
  border: 1px solid transparent;
  border-radius: 4px;
  color: #a94442 !Important;
  background-color: #f2dede;
  border-color: #ebccd1;
  margin-top: 10px;
  padding: 10px 15px
}

.ninja-forms-form-wrap {
  .textarea-wrap textarea {
    height: 100px;
  }
}


